<template>
	<div>
		<ul id="nav" :style="change">
			<router-link to="/circle">
				<li>
					<!--<img src="../../static/img/tab_icon_bwq.png"/>-->
					<i class="iconfont">&#xe6a3;</i>
					<p><a href="#">百文圈</a></p>
				</li>
			</router-link >
			<router-link to="/frends">
				<li>
					<i class="iconfont">&#xe783;</i>
					<p><a href="#">好友</a></p>
				</li>
			</router-link>
			<router-link to="/home">
				<li>
					<i class="iconfont">&#xe6b8;</i>
					<p><a href="#">首页</a></p>
				</li>
			</router-link>
			<router-link to="/shopping">
				<li>
					<i class="iconfont">&#xe62c;</i>
					<p><a href="#">商城</a></p>
				</li>
			</router-link>
			<router-link to="/my">
				<li>
					<i class="iconfont">&#xe607;</i>
					<p><a href="#">我的</a></p>
				</li>
			</router-link>
		</ul>
	</div>
</template>

<script>
	import store from '../vuex/store'
	export default{
		name:'navs',
		store,
		data(){
			return{
			}
		},
		computed:{
			change(){
				return this.$store.state.footstate
			}
		}
	}
</script>

<style>
	*{padding: 0;margin: 0;}
	#nav{
		position: fixed;
		bottom: 0;
		left: 0;
		z-index: 999;
		display: flex;
		justify-content: space-around;
		width: 100%;
		height: 145px;
		align-items: center;
		background: #ccc;
	}
	#nav li{
		list-style: none;
		font-size: 35px;
		text-align: center;
	}
	#nav li i{
		font-size:65px;
	}
	a{
		text-decoration: none;
	}
	#nav .router-link-active{
		color: skyblue;
	}
	#nav .router-link-active p a{
		color: skyblue;
	}
</style>


