<template>
	<div>
		<div class="verify-head">
			<p>快速注册</p>
		</div>
		<div class="verify-cont">
				<div class="verify-input">
					<input ref="phone" type=""id="name" placeholder="输入邮箱或手机号"/>
					<input type=""id="verification" placeholder="输入验证码"/>
					<input type=""id="pass" placeholder="输入密码"/>
				</div>
		</div>
		<div style="width: 100%; height: 300px;"></div>
		<div class="verify-btn"><button @click="dd" id="verify">注册</button></div>
		<div v-if="xianliang" id="xianliang">{{err}}</div>
	</div>
</template>

<script>
	import store from '../vuex/store'
	export default{
		store,
		name:'verify',
		data(){
			return{
				xianliang:false,
				err:""
			}
		},
		methods:{
			dd(){
//				window.location.href="#/login";
			},
			data(data){
				this.xianliang=true
						this.err=data
						var that=this
						setTimeout(function(){
							that.xianliang=false
						},2000)
			}
			
		},
		//改变脚步公共状态
		mounted(){
			this.$store.commit('changefoot',"none")
			//手机号验证
			//手机判断
			var that=this

			var reg=/(^0{0,1}(13[0-9]|15[7-9]|153|156|18[7-9])[0-9]{8}$)|(^[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)*$)/i
			var reg1=/^[a-zA-Z\d_]{8,}$/
				$("#verify").click(function(){
					var name=$("#name").val()
					var pass=$("#pass").val()
					if(name==""){
//						alert("请输入手机号或邮箱")
						that.data("请输入手机号或邮箱")
					}
					else if(!reg.test(name)){
						that.data("请输入正确的邮箱或手机号")
					}
					else if(pass==""){
						that.data("请输入密码")
					}
					else if(!reg1.test(pass)){
						that.data("请输入正确的密码")
					}
					if(reg.test(name)&&reg1.test(pass)){
						var obj={
							name,
							pass
						}
						var objstr=JSON.stringify(obj)
						localStorage.setItem("reg",objstr)
						alert("注册成功")
						window.location.href="#/login"
					}
				})
		}
		
	}
</script>
	
<style scoped>
	body{
		background: #eeeeee;
	}
	#xianliang{
		width: 300px;
		height: 100px;
		background: black;
		position: fixed;
		top: 42%;
		left: 50%;
		margin-left: -150px;
		text-align: center;
		line-height: 100px;
		color: white;
	}
	*{
		padding: 0;
		margin: 0;
	}
	.verify-head{
		width: 100%;
		height: 680px;
		background: #28b1be;
		overflow: hidden;
	}
	.verify-head p{
		margin-top: 220px;
		font-size: 55px;
		color: white;
		text-align: center;
	}
	.verify-cont{
		width: 945px;
		height: 777px;
		position: fixed;
		top: 460px;
		left: 50%;
		margin-left:-462.5px ;
		background: url(../../static/img/bj.png)no-repeat;
		background-size: 945px 777px;
	}
	.verify-input{
		width: 850px;
		height: 500px;
		border: 2px solid #d0d0d0;
		border-radius: 20px;
		margin: 140px 0 0 40px;
	}
	.verify-input input{
		width: 780px;
		height: 170px;
		margin-left: 20px;
		padding-left: 140px;
		box-sizing: border-box;
		border: none;
		outline: none;
	}
	#name{
		background: url(../../static/img/dlzc_icon_zh.png)no-repeat center center;
		background-size: 70px 70px;
		background-position: 40px 50px;
		border-bottom: 2px solid #d0d0d0;
	}
	#pass{
		background: url(../../static/img/dlzc_icon_mm.png)no-repeat center center;
		background-size: 70px 70px;
		background-position: 40px 50px;
	}
	#verification{
		background: url(../../static/img/dlzc_icon_mm.png)no-repeat center center;
		background-size: 70px 70px;
		background-position: 40px 50px;
		border-bottom: 2px solid #d0d0d0;
	}
	.btn{
		margin-top: 80px;
	}
	.btn button{
		color: #35acb8;
		outline: none;
		border: none;
		background: white;
		font-size: 36px;
		
	}
	#name::-webkit-input-placeholder { 
	　　color:#cccccc;
		font-size: 35px;
　　}
	#pass::-webkit-input-placeholder { 
	　　color:#cccccc;
		font-size: 35px;
　　}
	#verification::-webkit-input-placeholder { 
		　　color:#cccccc;
			font-size: 35px;
	　　}
	.btn button:nth-child(1){
		float: left;
	}
	.btn button:nth-child(2){
		float: right;
	}
	.verify-btn{
		text-align: center;
	}
	#verify{
		width: 606px;
		height: 117px;
		background: #28b1be;
		outline: none;
		font-size: 50px;
		color: white;
	}
</style>


