<template>
	<div>
		<swiper :options="swiperOption"  ref="mySwiper" class="swiper">
			<!-- 这部分放你要渲染的那些内容 -->
			<swiper-slide v-for="item in items" class="swiper-slide">
				<img :src="item"/>
			</swiper-slide>
			<!--<swiper-slide  class="swiper-slide">
				<img src="../../build/logo.png"/>
			</swiper-slide>
			<swiper-slide  class="swiper-slide">
				<img src="../../build/logo.png"/>
			</swiper-slide>
			<swiper-slide  class="swiper-slide">
				<img src="../../build/logo.png"/>
			</swiper-slide>-->
			<!-- 这是轮播的小圆点 -->
			<div  class="swiper-pagination" slot="pagination"></div>
		</swiper>
		<!--导航-->
		<div class="pic-navs">
			<ul>
				<li>
					<span class="iconfont">&#xe783;</span>
					<span>我的好友</span>
					<span><img src="../../static/img/fif_list_but_jiantou.png"/></span>
				</li>
				<li>
					<span class="iconfont">&#xe60f;</span>
					<span>帮忙砍价</span>
					<span><img src="../../static/img/fif_list_but_jiantou.png"/></span>
				</li>
				<li>
					<span class="iconfont">&#xe609;</span>
					<span>附近好友</span>
					<span><img src="../../static/img/fif_list_but_jiantou.png"/></span>
				</li>
				<li>
					<span class="iconfont">&#xe638;</span>
					<span>好友管理</span>
					<span><img src="../../static/img/fif_list_but_jiantou.png"/></span>
				</li>
				<li>
					<span class="iconfont">&#xe65f;</span>
					<span>邀请好友</span>
					<span><img src="../../static/img/fif_list_but_jiantou.png"/></span>
				</li>
				<li>
					<span class="iconfont">&#xe7dc;</span>
					<span>添加好友</span>
					<span><img src="../../static/img/fif_list_but_jiantou.png"/></span>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
	export default {
		components:{
			swiper,
			swiperSlide
		},
		data() {
			return {
				items:["static/img/lb1.png","static/img/lb1.png","static/img/lb1.png"],
				swiperOption: {
				observer:true,//修改swiper自己或子元素时，自动初始化swiper 
				observeParents:true,//修改swiper的父元素时，自动初始化swiper 
				//是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true
				notNextTick: true,
		        pagination:{
		        	el:'.swiper-pagination',
		        },
		        autoplay:{
		        	delay:1000,
		        	disableOnInteraction:false
		        },
		        slidesPerView: 'auto',
		        centeredSlides: true,
		        paginationClickable: true,
		        spaceBetween: 30,
		            onSlideChangeEnd: swiper => {
		            	//这个位置放swiper的回调方法
		            	this.page = swiper.realIndex+1;
		            	this.index = swiper.realIndex;
		            }
		        },
		        //分页器
		        navOption: {
		        slidesPerView:4,
				notNextTick: true,
		        pagination:{
		        	el:'.swiper-pagination',
		        },
		        slidesPerView: 'auto',
		        centeredSlides: true,
		        paginationClickable: true,
		        spaceBetween: 30,
		            onSlideChangeEnd: swiper => {
		            	//这个位置放swiper的回调方法
		            	this.page = swiper.realIndex+1;
		            	this.index = swiper.realIndex;
		            }
		        }
			}
			
		},
		//定义这个sweiper对象
		computed: {
		    swiper() {
		      return this.$refs.mySwiper.swiper;
		    }
		},
		mounted () {
			//这边就可以使用swiper这个对象去使用swiper官网中的那些方法
		    this.swiper.slideTo(0, 0, false);
		}
 
	}
</script>
<style scoped>
	*{
		padding: 0;
		margin: 0;
	}
	.swiper{
		width: 100%;
		height: 600px;
	}
	.swiper-slide{
		width: 100%;
		height: 600px;
	}
	.swiper-slide img{
		width: 100%;
		height: 600px;
	}
	.swiper-pagination-bullet{
		width: 20px !important;
		height: 20px !important;
	}
	/*图片导航*/
	.pic-navs{
		width: 100%;
		height: 730px;
		border: 1px solid redd;
	}
	.pic-navs ul{
		width: 100%;
		height: 730px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	.pic-navs ul li{
		width: 525px;
		height: 200px;
		background: #eeeeee;
		display: flex;
		justify-content: space-around;
		align-items: center;
		margin-top: 30px;
	}
	.pic-navs ul li span:nth-child(1){
		font-size: 100px;
	}
	.pic-navs ul li span:nth-child(2){
		font-size: 50px;
	}
	.pic-navs ul li span:nth-child(3){
		font-size: 100px;
	}
</style>


