<template>
	<div>
		<div class="product-head">
			<div class="product-cart" :style="bgop1">
				<div>
					<img src="../../static/img/icon_title_back.png"/>
				</div>
				<div><img src="../../static/img/icon_title_gwc.png"/></div>
			</div>
			<div class="product-navs" :style="bgop">
				<div class="navs-up">
					<img src="../../static/img/tabbar_icon_title_back.png"/>
					<img :src="smallsrc1"/>
					<div></div>
				</div>
				<div class="navs-down">
					<span class="active">图文详情</span>
					<span>商品参数</span>
					<span>商品评论</span>
				</div>
			</div>
		</div>
		<!--轮播图-->
		<div class="swiper-container ss8">
				<div class="swiper-wrapper">
					<div class="swiper-slide" v-for="smsrc in smallsrc">
						<img :src="smsrc"/>
					</div>
				</div>
				<!--分页器-->
				  <div class="swiper-pagination"></div>
			</div>
			<!--分享链接-->
			<div class="share-link">
				<img src="../../static/img/xq10.png"/>
			</div>
			<!--商品描述-->
			<div class="shop-content">
				<div class="shop-name">
					<p class="names">{{name}}</p>
					<div class="bargain">
						<p>￥{{price}}</p>
						<p>限购{{purchase}}个</p>
						<span>砍价购买</span>
					</div>
					<div class="reduced">
						<p>现已优惠:<span>{{discounts}}</span></p>
						<p>已售{{sold}}件</p>
					</div>
					<div class="retail-price">
						零售价:<span>￥{{retailprice}}</span>
					</div>
				</div>
			</div>
			<!--分享链接2-->
			<div class="link-tow">
				<img src="../../static/img/xq6.png"/>
			</div>
			<!--图文详情-->
			<div class="particulars banner">
					<div class="title">图文详情</div>
					<div class="logo">
						<img src="../../static/img/xq7.png"/>
					</div>
					<!--商品信息图片-->
					<div class="shop-pics">
						<div v-for="spic in shoppics"><img :src="spic"/></div>
					</div>
					<!--商品图片细节-->
					<div class="xijie">
						<img src="../../static/img/xq5.png"/>
					</div>
					<div class="xiejie-pic">
						<div v-for="xjpic in xiejiepic"><img :src="xjpic"/></div>
					</div>
				</div>
				<!--商品参数-->
				<div class="com-par banner">
					<p>商品参数</p>
					<div class="com-par-font">
						<div>产品类别:{{category}}</div>
						<div>结构:单层</div>
						<div>使用场所:户外</div>
						<div>使用对象:通用</div>
						<div>图案:渐变色</div>
						<div>形状:圆柱形</div>
						<div>容量:500ml</div>
						<div>被子附件:有盖</div>
					</div>
				</div>
				<!--商品评论-->
				<div class="discuss banner">
					<p>商品评论</p>
					<div style="border-bottom: 1px solid #ccc;">
						<div class="discuss-conten">
							<div class="discuss-head"><img src="../../static/img/as1.png"/></div>
							<div class="discuss-mes">
								<div class="discuss-name">匿名</div>
								<div class="discuss-day">2018-8-8</div>
								<div class="discuss-font">只有傻子才去买</div>
							</div>
						</div>
					</div>
				</div>
				<div class="by">
					<div>立即分享</div>
					<div>砍价购买</div>
				</div>
	</div>
</template>

<script>
	import store from '../vuex/store'
	export default{
		data(){
			return{
				op:'0',
				op1:'1',
				bg:"0",
				pid:'',
				name:'',
				price:'',
				purchase:'',
				retailprice:'',
				discounts:'',
				category:'',
				sold:'',
				smallsrc:[],
				smallsrc1:'',
				shoppics:[],
				xiejiepic:[],
		        bgop:{
		        	opacity:this.op,
		        	background:"rgba(255,255,255,"+this.bg+")"
		        },
		        bgop1:{
		        	opacity:this.op1
		        }
			}
		},
		store,
		mounted(){
			//购物车数据
			//获取按钮点击事件
			var localStorage=window.localStorage.getItem('datas')
				 //如果没拿到localStorage--第一次添加localStorage
				if(localStorage==undefined){
					//设置cookie=datas=[];
					window.localStorage.setItem("datas","[]");
				}
			var localStoragearr=JSON.parse(localStorage);
			var that=this
			$(".by :last-child").click(function(){
				//获取购物车需要的数据
				if(checkishas(that.pid)){
								//把该商品的count+1
								updateNum(that.pid,1,that.purchase);
								console.log(localStoragearr)
							}else{
							//存储到obj对象中
								var obj={
									pid:that.pid,
									imgSrc:that.smallsrc1,
									name:that.name,
									price:that.price,
									purchase:that.purchase,
									discounts:that.discounts,
									pcount:1
								}
							//设置localstorage--从新获取localstorage
								var locals=window.localStorage.getItem("datas");	
								var localsarr=JSON.parse(locals);
							//像数组中添加本次的商品信息的obj
								localsarr.push(obj);
							//把数组转回字符串
								var localsarrstr=JSON.stringify(localsarr);
							//添加到localStorage中
								window.localStorage.setItem("datas",localsarrstr);
							//点击完成从新获取总数
							}
//							var localstorage =window.localStorage.getItem("datas")
//							var localstoragearr = JSON.parse(localstorage)
//							console.log(localstoragearr)
//							$.each(localstoragearr, function(index, ele) {
//								if(ele.pid == this.pid) {
//									if(ele.pcount<ele.purchase)
//									alert(1)
//
//									ele.pcount = pid.pcount
//								localstorage = JSON.stringify(localstoragearr)
//									localStorage.datas = localstorage
//								}
//			
//							});
							window.location.href='#/cars'
//							console.log(window.localStorage.setItem("datas"))
				})
			this.$store.commit('changefoot',"none")
			//点击跳转
			var productcart=document.querySelectorAll(".product-cart div")
			var that = this;
			productcart[0].onclick=function(){
				that.$router.go(-1)
			}
			productcart[1].onclick=function(){
				window.location.href='#/cars'
			}
			//获取pid
			this.pid=this.$route.params.pid
//			console.log(this.pid)
			//获取数据
			var that=this;
			this.$axios({
				url:"../../static/mydetail.json",
				method:"get",
				dataType:"json"
			}).then(function(res){
				var detail=res.data.detail;
//				console.log(detail)
				$.each(detail, function(index,ele) {
//					console.log(ele.pid)
					if(ele.pid==that.pid){
						that.smallsrc=ele.smallsrc
						that.smallsrc1=ele.smallsrc[0]
						that.shoppics=ele.shoppics
						that.xiejiepic=ele.xiejiepic
						that.xiejiepic=ele.xiejiepic
						that.name=ele.name
						that.price=ele.price
						that.purchase=ele.purchase
						that.retailprice=ele.retailprice
						that.category=ele.category
						that.discounts=ele.discounts
						that.sold=ele.sold
					}
				});
			})
			//页面滚动时换导航的opacity
			var that=this;
			this.bgop1.opacity=1;
			window.onscroll=function(){
				var scrollTop=document.documentElement.scrollTop;
				that.bgop.background="rgba(255,255,255,"+scrollTop*0.005+")"
				that.bgop.opacity=scrollTop*0.1
				that.bgop1.opacity=1-(scrollTop*0.1);
			}
			//轮播图
			//轮播分组二
			
				//楼层滚动事件
				 var flag = true //设置标识。防止出现跑马灯 
            $(".navs-down span").click(function(){
                flag = false  
                $(this).addClass("active").siblings().removeClass("active")  
                var index = $(this).index()//获取当前点击元素的索引  
                var top = $(".banner").eq(index).offset().top;//获取每个banner到顶部的距离  
                $("html,body").stop(true).animate({"scrollTop":top},function(){flag = true})  
            })  
            
//          滚轮滑动切换楼层  
            $(window).scroll(function(){  
                if(flag){  
                //浏览器可视窗口的一半，也可以根据自己需求设定  
                    var winH = $(window).innerHeight()/2;  
                    var scrollT = $(window).scrollTop()  
                    var len = $(".banner").size()  
                    for(var i=0;i<len;i++){  
                        //注意这里banner对象加了i之后变成了js对象,所以用offsetTop  
                        var bannerGap = $(".banner")[i].offsetTop - scrollT  
                        if(bannerGap < winH){  
                            $(".navs-down span").eq(i).addClass("active").siblings().removeClass("active")  
                        }  
                    }  
                }  
            })  
				var navswiper1=new Swiper('.ss8',{
					observer:true,//修改swiper自己或子元素时，自动初始化swiper 
					observeParents:false,//修改swiper的父元素时，自动初始化swiper 	
				//分组
//				loop:true,
					pagination : '.swiper-pagination'
				})

		}
	}
</script>

<style scoped>
	*{
		padding: 0;
		margin: 0;
	}
	
	.product-cart{
		width: 100%;
		height: 100px;
		position: fixed;
		top: 0;
		z-index: 998;
		opacity:1;
	}
	/*.product-cart div{
		overflow: hidden;
	}*/
	.product-cart div img{
		width: 110px;
		height: 100px;
	}
	.product-cart div:nth-child(1){
		float: left;
	}
	.product-cart div:nth-child(2){
		float: right;
	}
	.product-navs{
		width: 100%;
		height: 200px;
		position: fixed;
		z-index: 888;
		top: 0;
		opacity: 0;
		background: rgba(255,255,255,0);
	}
	.navs-up{
		width: 100%;
		height: 120px;
		line-height: 120px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.navs-up img:nth-child(1){
		width: 80px;
		height: 80px;
		line-height: 120px;
	}
	.navs-up img:nth-child(2){
		width: 100px;
		height: 120px;
		margin-left: -70px;
	}
	.navs-down{
		width: 100%;
		height: 80px;
		display: flex;
		justify-content: space-between;
		text-align: center;
		line-height: 80px;
		font-size: 35px;
	}
	.navs-down span{
		width: 33%;
		height: 80px;
	}
	.navs-down span:nth-child(2){
		/*padding-left: 80px;
		box-sizing: border-box;*/
	}
	.active{
		border-bottom: 1px solid deepskyblue;
	}
	/*轮播图*/
	.ss8 .swiper-wrapper{
		width: 100%;
	}
	.ss8 .swiper-wrapper img{
		width: 100%;
		height: 900px;
		vertical-align: top;
	}
	/*分享链接*/
	.share-link img{
		width: 100%;
		height: 160px;
		vertical-align: top;
	}
	/*商品信息*/
	.shop-content{
		width:100%;
		height: 520px;
		overflow: hidden;
	}
	.shop-name{
		width: 90%;
		height: 380px;
		margin: 0 auto;
		margin-top: 65px;
	}
	.names{
		font-weight: bold;
		font-size: 45px;
	}
	.bargain{
		overflow: hidden;
		margin:20px 0 80px 0;
	}
	.bargain p:nth-child(1){
		color: #1adae9;
		float: left;
		font-size: 45px;
		margin-right: 20px;
	}
	.bargain p:nth-child(2){
		float: left;
		width: 200px;
		height: 60px;
		text-align: center;
		line-height: 60px;
		border: 2px solid #ccc;
		color: #ccc;
		border-radius: 20%;
	}
	.bargain span{
		float: right;
		font-size: 45px;
		line-height: 60px;
		color: #1adae9;
	}
	.reduced{
		width: 100%;
		overflow: hidden;
		margin-bottom: 40px;
	}
	.reduced p:nth-child(1){
		float: left;
		color: #ccc;
		font-size: 35px;
	}
	.reduced p:nth-child(1) span{
		font-size: 35px;
		color: #1adae9;
		margin-left: 20px;
	}
	.reduced p:nth-child(2){
		float: right;
		color: #ccc;
		font-size: 35px;
	}
	.retail-price{
		font-size: 35px;
		color: #ccc;
	}
	.retail-price span{
		color: #1adae9;
		margin-left: 30px;
	}
	/*分享链接二*/
	.link-tow img{
		width: 100%;
		height: 120px;
		vertical-align: top;
	}
	/*图文详情*/
	.title{
		width: 100%;
		height:160px;
		font-size: 40px;
		text-align: center;
		line-height: 160px;
		background: #ccc;
	}
	.logo img{
		width: 100%;
		height: 710px;
		vertical-align: top;
	}
	/*商品图片信息*/
	.shop-pics img{
		width: 100%;
		height: 800px;
		vertical-align: top;
	}
	/*商品图片细节*/
	.xijie img{
		width: 100%;
		height: 300px;
		vertical-align: top;
	}
	.xiejie-pic img{
		width: 100%;
		height: 800px;
		vertical-align: top;
	}
	.com-par>p{
		width: 100%;
		height: 100px;
		text-align: center;
		line-height: 100px;
		background: #ccc;
		font-size: 40px;
	}
	/*参数*/
	.com-par-font{
		width: 100%;
		height:800px;
	}
	.com-par-font div{
		width: 100%;
		height: 100px;
		border-bottom: 1px solid #ccc;
		font-size: 30px;
		line-height: 100px;
		padding-left: 50px;
	}
	/*评论*/
	.discuss{
		height: 10000px;
	}
	.discuss>p{
		width: 100%;
		height: 100px;
		text-align: center;
		line-height: 100px;
		background: #ccc;
		font-size: 40px;
	}
	.discuss-conten{
		width: 95%;
		height: 260px;
		margin: 0 auto;
		padding-top: 20px;
		box-sizing: border-box;
	}
	.discuss-head{
		float: left;
		width: 160px;
		height: 160px;
	}
	.discuss-head img{
		width: 160px;
		height: 160px;
	}
	.discuss-mes{
		float: right;
		width: 80%;
		height: 180px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		color: #ccc;
		font-size: 25px;
	}
	.discuss-name{
		color: black;
		font-size: 40px;
	}
	.by{
		width: 100%;
		overflow: hidden;
		line-height: 100px;
		position: fixed;
		bottom: 0;
		text-align: center;
		font-size: 40px;
	}
	.by div:nth-child(1){
		float: left;
		width: 40%;
		height: 100px;
		color: green;
		background: white;
	}
	.by div:nth-child(2){
		float: right;
		width: 60%;
		height: 100px;
		color: white;
		background: green;
	}
</style>


