<template>
	<div>
		<!--头部分-->
		<scroller :on-infinite="infinite" :on-refresh="refresh" ref="myscroller">
			<div class="my-head">
				<div style="width: 50px;"></div>
				<span>我的</span>
				<i><img src="../../static/img/nav_icon_title_gwc.png"/></i>
			</div>
			<!--折叠面板-->
			<div id="navs">
				<el-collapse accordion>
					<el-collapse-item>
						<template slot="title">
							<div class="all">
								全部
							</div>
							<!--<i class="header-icon el-icon-info"></i>-->
						</template>
						<div class="navs-contend">
							<div @click="all">全部</div>
							<div @click="man">男性</div>
							<div @click="woman">女性</div>
							<div @click="lingshi">零食</div>
							<div @click="snacks">生活品</div>
							<div @click="digital">数码</div>
							<div @click="specialty">地方特产</div>
						</div>
					</el-collapse-item>
				</el-collapse>
			</div>
			<!--导航选择-->
			<!--上拉刷新下拉加载-->

			<div v-if="show" class="commodity-conten">
				<div ref="com" v-for="item in listdata" class="commodity" @click="mydata(item.pid)">
					<div class="commodity-pic"><img :src="item.imgsrc" /></div>
					<div class="shop-des">
						<p class="shop-name">
							{{item.des}}
						</p>
						<p class="sponsor">赞助商:</p>
						<div class="kanjia">
							<p>{{item.price}}</p>
							<p>砍价购买</p>
						</div>
					</div>
				</div>
			</div>
			<!--点击出现相对应的数据-->
			<div v-if="hiddent" class="commodity-conten">
				<div ref="com" v-for="item in list" class="commodity" @click="mydata(item.pid)">
					<div class="commodity-pic"><img :src="item.imgsrc" /></div>
					<div class="shop-des">
						<p class="shop-name">
							{{item.des}}
						</p>
						<p class="sponsor">赞助商:</p>
						<div class="kanjia">
							<p>{{item.price}}</p>
							<p>砍价购买</p>
						</div>
					</div>
				</div>
			</div>
			<!--全部-->
			<div v-if="quan" class="commodity-conten">
				<div ref="com" v-for="item in listdata" class="commodity" @click="mydata(item.pid)">
					<div class="commodity-pic"><img :src="item.imgsrc" /></div>
					<div class="shop-des">
						<p class="shop-name">
							{{item.des}}
						</p>
						<p class="sponsor">赞助商:</p>
						<div class="kanjia">
							<p>{{item.price}}</p>
							<p>砍价购买</p>
						</div>
					</div>
				</div>
			</div>
		</scroller>
	</div>
</template>
<script>
	import $ from 'jquery'
	import store from '../vuex/store'
	export default {
		data() {
			return {
				list: '',
				activeNames: ['1'],
				listdata: [],
				noData: "",
				num: 1,
				pid: '',
				list:'',
				show:true,
				hiddent:false,
				quan:false,
				allnum:1,
			}
		},
		store,
		methods: {
			handleChange(val) {
				console.log(val);
			},
			//获取单个数据
			getListData(key) {
				var that = this
				this.$axios({
					url: "../../static/list.json",
					method: "get",
					dataType: "json"
				}).then(function(res) {
					that.list = res.data.all[0][key]
				})
			},
			//全部默认加载第一项
			all() {
				this.show=false
				this.hiddent=false
				this.quan=true
				this.getlist(this.allnum)
			},
			//男士数据
			man(){
				this.show=false
				this.quan=false
				this.hiddent=true
				this.getListData("aa2")
			},
			//生活用品数据
			snacks() {
				this.show=false
				this.quan=false
				this.hiddent=true
				this.getListData("aa1")
			},
			//女士数据
			woman() {
				this.show=false
				this.quan=false
				this.hiddent=true
				this.getListData("aa3")
			},
			//零食数据
			lingshi() {
				this.show=false
				this.quan=false
				this.hiddent=true
				this.getListData("aa4")
			},
			//数码数据
			digital(){
				this.show=false
				this.quan=false
				this.hiddent=true
				this.getListData("aa5")
			},
			//特产数据
			specialty() {
				this.show=false
				this.quan=false
				this.hiddent=true
				this.getListData("aa6")
			},
			mydata(pid) {
				this.$router.push({
					name: 'product',
					params: {
						pid: pid
					}
				})
				//				this.aa='/product/'+this.pid
				//				console.log(this.pid)
			},
			//			获取多个数据
			getlist(num) {
				var that = this;
				this.$axios({
					url: "../../static/list.json",
					method: "get",
					dataType: "json"
				}).then(function(res) {
					that.listdata = that.listdata.concat(res.data.all[0]["aa" + num]);
				}).catch(function(err) {
					console.log(err)
				})
			},
			//--上拉------------------------------
			infinite(done) {
				this.num++
					if(this.noData) {
						setTimeout(() => {
							//结束上拉
							this.$refs.myscroller.finishInfinite(2);
						})
						return;
					}
				let that = this;
				let start = this.listdata;
				setTimeout(() => {
					//请求第二页数据 像原数据中添加
					that.getlist(this.num);
					if(this.num > 5) {
						that.noData = "没有更多数据"
					}
					//自动适应大小
					that.$refs.myscroller.resize();
					done()

				}, 1500)
			},
			refresh() {
				//下拉刷新
				var that = this;
				setTimeout(function() {
					//停止拉下
					that.$refs.myscroller.finishPullToRefresh()
				}, 2500)

			}
		},
		mounted() {
			this.$store.commit('changefoot', "flex")
			//导航栏点击事件
			var divs = document.querySelectorAll(".navs-contend div")
			var all = document.getElementsByClassName("all")
			var itemcont = document.querySelector("#navs .el-collapse-item__content")
			for(var i = 0; i < divs.length; i++) {
				divs[i].index = i;
				divs[i].onclick = function() {
					all[0].innerText = divs[this.index].innerText;
					//					itemcont.style.display='none';
				}
			}
			this.getlist(this.num)
			all[0].onclick = function() {
				//				itemcont.style.display='block';
			}
			var that = this
			this.$nextTick(function() {})
			//			页面刷新获取初始数据
			//			this.getlist(this.num);
		}
	}
</script>

<style>
	* {
		padding: 0;
		margin: 0;
	}
	
	.my-head {
		width: 100%;
		height: 130px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	
	.my-head span {
		font-size: 50px;
	}
	
	.my-head i img {
		width: 70px;
		height: 70px;
		margin-right: 10px;
	}
	/*商品列表*/
	
	.commodity-conten {
		background: #ccc;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	
	.commodity {
		width: 49%;
		height: 820px;
		margin-top: 20px;
		background: white;
	}
	
	.commodity-pic {
		width: 100%;
		height: 520px;
	}
	
	.commodity img {
		width: 100%;
		height: 520px;
		float: left;
	}
	
	.shop-des {
		width: 500px;
		height: 300px;
		margin: 0 auto;
		padding-top: 35px;
		box-sizing: border-box;
	}
	
	.shop-name {
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		font-size: 30px;
	}
	
	.sponsor {
		margin: 35px 0 30px 0;
		color: #ccc;
	}
	
	.commodity .kanjia p:nth-of-type(1) {
		float: left;
		font-size: 40px;
	}
	
	.commodity .kanjia p:nth-of-type(2) {
		float: right;
		width: 170px;
		height: 60px;
		border: 2px solid #ccc;
		border-radius: 10%;
		text-align: center;
		line-height: 60px;
	}
	/*导航栏*/
	
	#navs {
		position: relative;
	}
	
	#navs .el-collapse {
		color: red;
		position: relative;
	}
	
	.el-collapse-item__arrow {
		float: left;
	}
	
	.all {
		float: left !important;
	}
	
	#navs.el-icon-arrow-right:before {
		content: "\E605";
	}
	
	#navs .el-collapse-item__header {
		width: auto;
		margin-top: 10px;
		font-size: 50px;
	}
	
	#navs .el-collapse-item__content {
		width: 100%;
		position: absolute;
		background: white;
		z-index: 999;
	}
	
	#navs .navs-contend {
		height: 200px;
	}
	
	#navs .navs-contend div {
		float: left;
		width: 230px;
		height: 120px;
		margin: 40px 30px 0 30px;
		border-radius: 20%;
		text-align: center;
		line-height: 140px;
		font-size: 30px;
		background: #f3f3f3;
		color: #0e0c0c;
	}
</style>


