<template>
	<div>
		<div class="login-head">
			<p>享佰购</p>
		</div>
		
			<div class="login-cont">
				<div class="login-input">
					<input type=""id="name" placeholder="输入邮箱或手机号"/>
					<input type=""id="pass" placeholder="输入密码"/>
					<div class="btn">
						<button @click="verify">快速注册</button>
						<button>重置密码</button>
					</div>
				</div>
			</div>
			<div style="width: 100%; height: 300px;"></div>
			<div class="login-btn"><button @click="dd" id="login">登录</button></div>
			<div v-if="xianliang" id="xianliang">{{err}}</div>
			
	</div>
</template>

<script>
	import store from '../vuex/store'
	export default{
		store,
		name:'login',
		data(){
			return{
				xianliang:false,
				err:''
			}
		},
		methods:{
			data(data){
				this.xianliang=true
						this.err=data
						var that=this
						setTimeout(function(){
							that.xianliang=false
						},2000)
			},
			dd(){
				var name=$("#name").val()
				var pass=$("#pass").val()
				var reg=window.localStorage.getItem("reg")
				var regarr=JSON.parse(reg)
				if(name==regarr.name&&pass==regarr.pass){
					window.location.href="#/home";
				}else if(name==""){
					this.data("用户名不能为空")
				}else if(pass==""){
					this.data("密码不能为空")
				}else if(name!=regarr.name){
					this.data("没用此用户")
				}else if(name!=regarr.pass){
					this.data("密码错误")
				}
			},
			verify(){
				window.location.href="#/verify";
			}
			
		},
		//改变脚步公共状态
		mounted(){
			this.$store.commit('changefoot',"none")
		}
		
	}
</script>
	
<style scoped>
	body{
		background: #eeeeee;
	}
	#xianliang{
		width: 300px;
		height: 100px;
		background: black;
		position: fixed;
		top: 42%;
		left: 50%;
		z-index: 999;
		margin-left: -150px;
		text-align: center;
		line-height: 100px;
		color: white;
	}
	*{
		padding: 0;
		margin: 0;
	}
	.login-head{
		width: 100%;
		height: 680px;
		background: #28b1be;
		overflow: hidden;
	}
	.login-head p{
		margin-top: 220px;
		font-size: 55px;
		color: white;
		text-align: center;
	}
	.login-cont{
		width: 945px;
		height: 777px;
		position: fixed;
		top: 460px;
		left: 50%;
		margin-left:-462.5px ;
		background: url(../../static/img/bj.png)no-repeat;
		background-size: 945px 777px;
	}
	.login-input{
		width: 850px;
		height: 350px;
		border: 2px solid #d0d0d0;
		border-radius: 20px;
		margin: 140px 0 0 40px;
	}
	.login-input input{
		width: 780px;
		height: 170px;
		margin-left: 20px;
		padding-left: 140px;
		box-sizing: border-box;
		border: none;
		outline: none;
	}
	#name{
		background: url(../../static/img/dlzc_icon_zh.png)no-repeat center center;
		background-size: 70px 70px;
		background-position: 40px 50px;
		border-bottom: 2px solid #d0d0d0;
	}
	#pass{
		background: url(../../static/img/dlzc_icon_mm.png)no-repeat center center;
		background-size: 70px 70px;
		background-position: 40px 50px;
	}
	.btn{
		margin-top: 80px;
	}
	.btn button{
		color: #35acb8;
		outline: none;
		border: none;
		background: white;
		font-size: 36px;
		
	}
	#name::-webkit-input-placeholder { 
	　　color:#cccccc;
		font-size: 35px;
　　}
	#pass::-webkit-input-placeholder { 
	　　color:#cccccc;
		font-size: 35px;
　　}
	.btn button:nth-child(1){
		float: left;
	}
	.btn button:nth-child(2){
		float: right;
	}
	.login-btn{
		text-align: center;
	}
	#login{
		width: 606px;
		height: 117px;
		background: #28b1be;
		outline: none;
		font-size: 50px;
		color: white;
	}
</style>


