import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const state={
	//脚步的样式
	footstate:{
		display:"flex"
	}
}
const mutations={
	//改变脚步样式
	changefoot(state,str){
		state.footstate.display=str;
	}
}
export default new Vuex.Store({
	state,
	mutations
})



