<template>
	<div>
		<!--导航部分-->
		<div class="my-head">
			<i><img src="../../static/img/tabbar_icon_title_back.png"/></i>
			<span>购物车</span>
			<a href="#" @click="toggle">编辑</a>
		</div>
		<!--购买的商品-->
		<div class="shopping" v-for="item in localstoragearr">
			<div class="dividing"></div>
			<div class="shopping-content">
				<div v-if="flag" class="del" @click="del(item)"><img src="../../static/img/gwc_icon_ljx.png"/></div>
				<div class="shopping-pic">
					<img :src="item.imgSrc" />
				</div>
				<div class="shopping-des">
					<div class="brief">
						<div class="brief-left">{{item.name}}</div>
						<div class="brief-right">有库存</div>
					</div>
					<!--价格和数量-->
					<div class="shopping-num">
						<p class="price">{{item.price}}</p>
						<div class="count">
							<span @click="jian(item)">-</span>
							<span>{{item.pcount}}</span>
							<span @click="add(item)">+</span>
						</div>
					</div>
					<!--优惠价格-->
					<div class="discounts">
						<div class="Have-discount">可优惠&nbsp;&nbsp;<span>{{item.discounts}}</span></div>
						<div class="Can-discount">已优惠&nbsp;&nbsp;<span>0.00</span></div>
					</div>
					<!--分享时间-->
					<div class="share">分享已结束</div>
				</div>
			</div>
			<!--好友共享价格-->
			<div class="frends-share">
				<div class="share-price">与3个好友分享,共享价￥108</div>
				<div class="share-count"><span>已0个</span><span>继续分享</span></div>
			</div>
		</div>
		<div class="by">
			<div>合计：{{total}}</div>
			<div>结算</div>
		</div>
		<div v-if="xianliang" id="xianliang">该商品限购次数:{{pcount}}</div>
	</div>
</template>

<script>
	import store from '../vuex/store'
	export default {
		data() {
			return {
				flag:false,
				localstoragearr:'',
				num:0,
//				pcount:'',
				xianliang:false
			}
		},
		store,
		methods: {
			jian(item) {
				item.pcount--
					if(item.pcount <= 1) {
						item.pcount = 1
					}
				 this.aa(item)
			},
			//时时修改pcount
			aa(pid){
				 	var localstorage = localStorage.getItem("datas")
					var localstoragearr = JSON.parse(localstorage)
					$.each(localstoragearr, function(index, ele) {
						if(ele.pid == pid.pid) {
							ele.pcount = pid.pcount
							localstorage = JSON.stringify(localstoragearr)
							localStorage.datas = localstorage
						}
	
					});
				},
			add(item) {
				item.pcount++
					if(item.pcount >= item.purchase) {
						item.pcount = item.purchase
						this.pcount=item.pcount
						this.xianliang=true
						var that=this
						setTimeout(function(){
							that.xianliang=false
						},2000)
					}
				 this.aa(item)
			},
			del:function(item){
					delProduct(item.pid)
					this.localstoragearr.splice(item,1)
//					var localstoragestr=JSON.stringify(this.localstoragearr)
//					console.log(localstoragestr)
//					localstorage.setItem("datas",localstoragestr);
//					window.localstorage.datas=localstoragestr
					this.total
				},
			toggle(){
				this.num++
				if(this.num%2==0){
					this.flag=true
				}else{
					this.flag=false
				}

			}
		},
		computed: {
			total(){
				var num = 0
				$.each(this.localstoragearr, function(index, ele) {
					num += ele.price * ele.pcount
				});
				return num
			}
		},
		mounted() {
			this.$store.commit('changefoot', "none")
			var that = this;
			var localstorage = window.localStorage.getItem("datas")
			var localstoragearr = JSON.parse(localstorage)
			this.localstoragearr = localstoragearr
			console.log(localstoragearr)
			//点击跳转
			$(".my-head i").click(function() {
//				window.location.href="#/product"
				that.$router.go(-1)
			})
		}
	}
</script>

<style scoped>
	#xianliang{
		width: 300px;
		height: 100px;
		background: black;
		position: fixed;
		top: 42%;
		left: 50%;
		margin-left: -150px;
		text-align: center;
		line-height: 100px;
		color: white;
	}
	/*头部导航*/
	
	.my-head {
		width: 100%;
		height: 130px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	
	.my-head i img {
		width: 70px;
		height: 70px;
		margin-left: 45px;
	}
	
	.my-head span {
		font-size: 50px;
	}
	
	.my-head a {
		font-size: 40px;
		color: #1cd3e4;
		margin-right: 20px;
	}
	/*商品*/
	
	.dividing {
		width: 100%;
		height: 15px;
		background: #eeeeee;
	}
	
	.shopping {
		width: 100%;
		height: 600px;
	}
	
	.shopping-content {
		width: 100%;
		height: 480px;
		padding-top: 30px;
		box-sizing: border-box;
	}
	.del{
		float: left;
		width: 50px;
		height: 50px;
		line-height: 300px;
		/*display: none;*/
	}
	.del img{
		width: 50px;
		height: 50px;
	}
	.shopping-pic {
		width: 280px;
		height: 280px;
		float: left;
		margin-left: 20px;
	}
	
	.shopping-pic img {
		width: 280px;
		height: 280px;
	}
	
	.shopping-des {
		width: 670px;
		height: 420px;
		margin-right: 45px;
		float: right;
		overflow: hidden;
	}
	
	.brief {
		width: 100%;
		height: 195px;
		margin-top: 10px;
	}
	
	.brief-left {
		float: left;
		width: 540px;
		font-size: 35px;
		color: #666666;
	}
	
	.brief-right {
		float: right;
		font-size: 30px;
		color: #666666;
	}
	
	.shopping-num {
		width: 100%;
		height: 77px;
	}
	
	.price {
		float: left;
		color: #28B1BE;
		font-size: 45px;
		margin-top: 15px;
	}
	
	.count {
		float: right;
		width: 300px;
		height: 100%;
		text-align: center;
		border: 1px solid #e1e1e1;
		display: flex;
		justify-content: space-evenly;
		align-items: center;
		font-size: 50px;
	}
	
	.count span:nth-of-type(2) {
		width: 130px;
		border-right: 1px solid #e1e1e1;
		border-left: 1px solid #e1e1e1;
	}
	/*优惠部分*/
	
	.discounts {
		width: 470px;
		height: 60px;
		font-size: 40px;
		overflow: hidden;
	}
	
	.Have-discount {
		float: left;
	}
	
	.Have-discount span,
	.Can-discount span {
		color: #20b9c8;
	}
	
	.Can-discount {
		float: right;
	}
	/*分享*/
	
	.share {
		width: 230px;
		height: 70px;
		background: #dddddd;
		border-radius: 10px;
		float: right;
		text-align: center;
		line-height: 70px;
		color: #9e9e9e;
	}
	/*好友共享*/
	
	.frends-share {
		width: 100%;
		height: 100px;
		background: #c5eef2;
		font-size: 35px;
		color: #0cdaee;
		line-height: 100px;
	}
	/*好友分享价格*/
	
	.share-price {
		float: left;
	}
	
	.share-count {
		float: right;
	}
	
	.by {
		width: 100%;
		overflow: hidden;
		line-height: 100px;
		position: fixed;
		bottom: 0;
		text-align: center;
		font-size: 40px;
	}
	
	.by div:nth-child(1) {
		float: left;
		width: 60%;
		height: 100px;
		color: green;
		background: white;
	}
	
	.by div:nth-child(2) {
		float: right;
		width: 40%;
		height: 100px;
		color: white;
		background: green;
	}
</style>


